<template>
	<h3 class="indentStatus">
		<slot name="indentName"></slot>
		<ul >
			<li>
				<img src="http://temp.im/120x20/007AFF/fff" alt="" v-if="routename != '/shopCart'">
				<img src="http://temp.im/120x20/007eee/1c4" alt="" v-if="routename == '/shopCart'">
			</li>
			<li>
				<img src="http://temp.im/120x20/007AFF/fff" alt="" v-if="routename != '/order'">
				<img src="http://temp.im/120x20/007eee/1c4" alt="" v-if="routename == '/order'">
			</li>
			<li>
				<img src="http://temp.im/120x20/007AFF/fff" alt="" v-if="routename != 'shopCart'">
				<img src="http://temp.im/120x20/007eee/1c4" alt="" v-if="routename == 'shopCart'">
			</li>
		</ul>
	</h3>

</template>

<script>
	export default {
		name: 'indentStatus',
		data() {
			return {
				routename: this.$route.path
			}
		},
		created() {
			this.getPath()
		},
		methods:{
			getPath() {
			// console.log(this.$route.path)

			}
		}
	}
</script>

<style scoped>
	.indentStatus{
		font-weight: 600;
		font-size: 20px;
		text-align: left;
		color:#333;
		height: 38px;
		padding-top: 18px;
		background: #f3f3f3;
	}
	.indentStatus ul{
		float: right;
	}
	.indentStatus li {
		display: inline-block;
	}
</style>


